// Jest 전역 객체를 먼저 설정
import * as jestGlobals from '@jest/globals';
global.expect = jestGlobals.expect;
global.test = jestGlobals.test;
global.describe = jestGlobals.describe;
global.beforeEach = jestGlobals.beforeEach;
global.afterEach = jestGlobals.afterEach;
global.jest = jestGlobals.jest;

// React 기본 설정
import React from 'react';
import ReactDOM from 'react-dom';

// 글로벌 설정
global.__DEV__ = true;
global.window = global.window || {};
global.React = React;
global.ReactDOM = ReactDOM;

// 콘솔 메시지 필터링
const originalConsoleError = console.error;
console.error = (...args) => {
  const errorMessage = args[0] || '';
  
  // React 내부 오류 무시
  if (errorMessage && typeof errorMessage === 'string' && 
      (errorMessage.includes('ReactCurrentDispatcher') || 
       errorMessage.includes('ReactCurrentOwner') ||
       errorMessage.includes('act(') ||
       errorMessage.includes('inside a test was not wrapped in act'))) {
    return;
  }
  
  originalConsoleError(...args);
};

// 타이머 모킹
jest.useFakeTimers();

